import axios from "axios";
import baseUrl from "./baseUrl";
import {
  reqIntercept,
  resIntercept,
  resErrorHandler,
  reqErrorHandler
} from "../interceptors";
const instance = axios.create({
  baseURL: baseUrl
});

instance.defaults.headers = {
  "Content-Type": "application/x-www-form-urlencoded"
};
instance.interceptors.request.use(reqIntercept, reqErrorHandler);
instance.interceptors.response.use(resIntercept, resErrorHandler);

if (!window.Promise) {
  window.Promise = Promise;
}
window.Promise.prototype.finally =
  window.Promise.prototype.finally ||
  {
    finally(fn) {
      const onFinally = cb => window.Promise.resolve(fn()).then(cb);
      return this.then(
        result => onFinally(() => result),
        reason =>
          onFinally(() => {
            throw reason;
          })
      );
    }
  }.finally;

export default instance;
