const OSS = require("ali-oss");
import { $get } from "@/core/axios/methods/index";
class Myoss {
  static client = null;

  static getToken() {
    return $get("/ali/getToken").then((res) => {
      return res;
    });
  }

  static async initClient() {
    let getToken = this.getToken;
    let info = await getToken();
    info = info.data;
    console.log(info);

    this.client = new OSS({
      // yourRegion填写Bucket所在地域。以华东1（杭州）为例，Region填写为oss-cn-hangzhou。
      region: "oss-cn-hangzhou",
      // 从STS服务获取的临时访问密钥（AccessKey ID和AccessKey Secret）。
      accessKeyId: info.AccessKeyId,
      accessKeySecret: info.AccessKeySecret,
      // 从STS服务获取的安全令牌（SecurityToken）。
      stsToken: info.SecurityToken,
      refreshSTSToken: async () => {
        // 向您搭建的STS服务获取临时访问凭证。
        let info = await getToken();
        info = info.data;
        return {
          accessKeyId: info.AccessKeyId,
          accessKeySecret: info.AccessKeySecret,
          stsToken: info.SecurityToken,
        };
      },
      // 刷新临时访问凭证的时间间隔，单位为毫秒。
      refreshSTSTokenInterval: 3500000,
      // 填写Bucket名称。
      bucket: "jojotest",
    });
  }

  // object表示上传到OSS的文件名称。
  // file表示浏览器中需要上传的文件，支持HTML5 file和Blob类型。
  static putFile(name, file) {
    let client = this.client;
    if (client) {
      return client
        .put(name, file)
        .then(function (res) {
          return {
            status:true,
            data:res
          }
        })
        .catch(function (err) {
          console.log(err);
          return false;
        });
    }
  }

  static getSignedUrl(url) {
    let client = this.client;
    if (client) {
      const data = client.signatureUrl(url);
      console.log(data);
    }
  }
}

export default Myoss;
