
import _ from "lodash"
import Vue from "vue"

let filenameRegex = /filename[^;=\n]*=((['"]).*?\2|[^;\n]*)/;

export const resErrorHandler = ({ response, message = "",  }) => {
  

  Vue.prototype.$notify.error({
    title: _.get(response, "data.error") || "出错了",
    message: String(_.get(response, "data.message") || message),
    duration: 5000
  });
  return Promise.reject(response || { message });
};

export const resIntercept = ({ headers, data = {},  }) => {
  
  if (
    (headers["content-type"] &&
      headers["content-type"].startsWith("application/vnd.ms-excel")) ||
    headers["content-type"].startsWith("application/octet-stream")
  ) {
    let filename = "文件.xlsx";
    let matches = filenameRegex.exec(headers["content-disposition"]) || [];
    if (matches[1]) filename = decodeURI(matches[1].replace(/['"]/g, ""));

    return {
      headers,
      data,
      filename
    };
  }

  return data;
};
