<template>
  <div id="app">
    <div v-if="this.$route.name != 'login'">
      <el-page-header :content="title" title="返回登录" @back="toLogin">
      </el-page-header>
      <div id="user">
        <span style="margin-right: 20px">{{name}}</span>
        <el-avatar :size="30" :src="circleUrl"> </el-avatar>
      </div>

      <div id="main" :style="{ height: height + 'px' }">
        <el-row style="height: 100%">
          <el-col :span="3"><Nav :height="height"></Nav></el-col>
          <el-col :span="21" class="content">
            <router-view :height="height"></router-view>
          </el-col>
        </el-row>
      </div>
    </div>

    <div id="main" :style="{ height: height + 'px' }" v-else>
      <el-row style="height: 100%">
        <el-col :span="24" >
          <router-view :height="height"></router-view>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import Nav from "@/pages/common/nav.vue";

export default {
  name: "App",
  components: {
    Nav,
  },
  data() {
    return {
      title: "",
      height: document.documentElement.clientHeight - 50,
      circleUrl: require("@/assets/images/head.jpg"),
      name:""
    };
  },
  watch: {
    $route(newVal) {
      this.title = newVal.meta.title;
    },
  },
  created() {
    let role=JSON.parse(localStorage.getItem("role"))
    this.name=role.name
  },
  methods:{
    toLogin(){
      console.log('返回登陆')
      this.$router.push({ name: 'login'})
    }
  }
};
</script>

<style>
body{
  margin:0 auto
}
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

#main .content {
  padding: 10px;
}
.el-page-header {
  line-height: 48px !important;
  background: #545c64;
  color: #fff;
  padding-left: 20px;
}
.el-page-header__content {
  color: #fff !important;
}
.el-table th {
  background-color: #eab463 !important;
  font-size: 14px;
  color: #f4f4f4 !important;
}
.el-table tr {
  font-size: 14px;
}
#user {
  position: absolute;
  top: 8px;
  right: 25px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 300px;
  color: #fff;
}
i{
  cursor:pointer;
  font-size:16px;
}
</style>
