import { $get, $post, $put, $delete } from "./methods";
import instance from "./Ajax/axios";
import _ from "lodash";
export default {
  install(Vue, network_default) {
    network_default && _.extend(instance.defaults, network_default);
    Vue.prototype.$axios = instance;
    Vue.prototype.$reqGet = $get;
    Vue.prototype.$reqPost = $post;
    Vue.prototype.$reqPut = $put;
    Vue.prototype.$reqDel = $delete;
  }
};
