import VueRouter from "vue-router";
import Vue from "vue";
Vue.use(VueRouter);
// 避免重复进入同一个路由地址
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch((err) => err);
};

const routes = [
  {
    path: "/",
    redirect: "/login", //默认显示
  },
  {
    path: "/login",
    name: "login",
    meta: {},
    component: () => import("@/pages/login/index"),
  },
  {
    path: "/user",
    name: "user",
    meta: {
      title: "用户管理",
    },
    component: () => import("@/pages/user/index"),
  },
  {
    path: "/books",
    name: "books",
    meta: {
      title: "书库",
    },
    component: () => import("@/pages/books/index"),
  },
  {
    path: "/authority",
    name: "authority",
    meta: {
      title: "权限管理",
    },
    component: () => import("@/pages/authority/index"),
  },
  {
    path: "/community",
    name: "community",
    meta: {
      title: "社群管理",
    },
    component: () => import("@/pages/community/index"),
  },
  {
    path: "/points/rule",
    name: "rule",
    meta: {
      title: "积分规则管理",
    },
    component: () => import("@/pages/points/rule"),
  },
  {
    path: "/points/management",
    name: "point-management",
    meta: {
      title: "积分说明",
    },
    component: () => import("@/pages/points/management"),
  },
  {
    path: "/recommend",
    name: "recommend",
    meta: {
      title: "荐书管理",
    },
    component: () => import("@/pages/recommend/index"),
  },
  {
    path: "/clockIn/index",
    name: "footage",
    meta: {
      title: "打卡素材管理",
    },
    component: () => import("@/pages/clockIn/index"),
  },
  {
    path: "/clockIn/data",
    name: "clockIn_data",
    meta: {
      title: "打卡数据管理",
    },
    component: () => import("@/pages/clockIn/data"),
  },
  {
    path: "/mall/book",
    name: "mall_book",
    meta: {
      title: "图书管理",
    },
    component: () => import("@/pages/mall/book"),
  },
  {
    path: "/mall/booklist",
    name: "booklist",
    meta: {
      title: "书单管理",
    },
    component: () => import("@/pages/mall/booklist"),
  },
  {
    path: "/mall/banner",
    name: "banner",
    meta: {
      title: "banner管理",
    },
    component: () => import("@/pages/mall/banner"),
  },
];
const router = new VueRouter({
  routes,
});

export default router;
