/**
 * 请求拦截器
 */

import { _ } from "core-js";

export const reqErrorHandler = error => {
  console.log("xixi: axios req error " + JSON.stringify(error));
  return Promise.reject(error);
};

export const reqIntercept = config => {
  if (
    config.headers["Content-Type"] == "application/x-www-form-urlencoded" &&
    config.method == "post" &&
    _.isObject(config.data)
  ) {
    _.each(config.data, (item, key) => {
      if (item == undefined) {
        config.data[key] = "";
      }
    });
    config.data = _.map(config.data, (v, k) => `${k}=${String(v) || ""}`).join(
      "&"
    );
    console.log(config);
  }
  return config;
};
